.Element {
  -moz-user-select: none;
 -khtml-user-select: none;
 -webkit-user-select: none;
 user-select: none;
}

.Element_Marker {
  width: 10px;
  height: 10px;
}

.Element_Title {
  display: flex;
}

.Element_Data {
  margin: 0 0 0 10px;
}

.DropedList_Title {
  margin: 10px auto 10px auto;
  font-size: 20px;
  font-weight: bold;
  text-align: center;
}

.DropedList {
  position: relative;
  width: 200px;
  background-color: #cccccc;

}

.Header {
  background-color: gray;
  display: flex;
  height: 20px;
}

table, th, td{
  border-collapse: collapse;
  vertical-align: center;
  border: 1px solid black;
}

.Add_Button {
  align-self: flex-end;
}
